body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: contain;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.fill {
  width: 100%;
  height: 100%;
  position: absolute;
}

.center {
  margin: auto;
  position: absolute;
  top: 0; left: 0; bottom: 0; right: 0;

  /* check dimensions */
  /* border: 1px #000 solid */
}

::-webkit-scrollbar {
  width: 10px;  /* width of the entire scrollbar */
}

::-webkit-scrollbar-track {
  background: #00000000;  /* color of the tracking area */
}

::-webkit-scrollbar-thumb {
  background-color: #a2a2a2;    /* color of the scroll thumb */
  border-radius: 20px;           /* roundness of the scroll thumb */
  border: 2px solid #000000;  /* creates padding around scroll thumb */
}

::-webkit-scrollbar-thumb:hover {
  background-color: #ffffff;  /* color of the tracking area */
}


/* --- World --- */

.world {
  width: 100%;
  height: 100%;
  position: absolute;
}

.bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

canvas {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #00000000;
}

.fpsCounter {
  top: 0px;
  font-weight: 100;
  color: #ffffff;
  position: absolute;
  background: #000000cd;
}